'use client'

import React, { useEffect, useState } from 'react'

interface MetadataPanelProps {
    urn: string
}

export default function MetadataPanel({ urn }: MetadataPanelProps) {
    const [data, setData] = useState<any>(null)
    const [error, setError] = useState<string|null>(null)

    useEffect(() => {
    if (!urn) return
    async function fetchMetadata() {
        try {
            const res = await fetch(`/api/models/${urn}/metadata/log`)
            const json = await res.json()
            console.log('Fetched metadata:', json)      // ← browser console
            setData(json)
        }catch (e: any) {
            console.error('Metadata fetch error:', e)  // ← browser console
            setError(e.message)
        }
    }
    fetchMetadata()
    }, [urn])

    if (!urn) return <div className="p-4">No model selected</div>
    if (error) return <div className="p-4 text-red-500">Error: {error}</div>
    if (!data) return <div className="p-4">Loading metadata…</div>

    return (
        <div className="overflow-auto p-4 text-sm bg-gray-50">
        <pre>{JSON.stringify(data, null, 2)}</pre>
    </div>
    )
}
